<template>
<div class="goods-detail-view">
	<div class="goods-header">
		<div class="goback-icon" @click="$router.go(-1)">
			<i class="iconfont icon-fanhui" />
		</div>
		<div class="goods-img">
			<img class="img-layout" :src="productDetail.large_img" alt="">
		</div>

		<div class="goods-title">
			{{productDetail.name}}
		</div>
	</div>

	<div class="goods-container">
		<div class="goods-detail">
			<div class="goods-desc">
				<div class="selection" v-for="(d,i) in descList" :key="i">
					<h3 class="desc">{{d.key}}</h3>
					<ul class="list">
						<li class="item" v-for="(item,index) in d.value" :key="index" :class="{active: d.currentIndex === index}" @click="selectItem(item,i,index)">{{item}}</li>
					</ul>
				</div>

				<div class="desc-container">
					<h3 class="c-name">商品描述</h3>
					<p class="c-desc" v-for="(g,i) in goodsDesc" :key="i">
						{{g}}
					</p>
				</div>
			</div>

			<div class="goods-footer">
				<div class="foods-info">
					<div class="info">
						<p class="price" :style="{marginBottom: selectArr.length > 0 ? '9px' : '0px'}">￥{{productDetail.price}}</p>
						<p class="sel">
							<span v-for="(s,i) in selectArr" :key="i">{{s}} </span>
						</p>
					</div>
					<van-stepper v-model="count" theme="round" button-size="18" min="1" max="99" disable-input />
				</div>
				<div class="footer-btn">
					<div class="collection" @click="collectionGoods">
						<i class="iconfont icon-xin1 rate" v-show="!bool" />
						<i class="iconfont icon-xin rate" v-show="bool" />
						<span class="content">收藏</span>	
					</div>
					<button class="btn shopcart" @click="addShopcart">
						加入购物车
					</button>
					<button class="btn buy" @click="buy">
						立即购买
					</button>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>
import '../assets/scss/goods_detail_view.scss'
import { utils } from '../assets/js/utils'
import {
	mapGetters
} from 'vuex'

export default {
	data() {
		return {
			id: "", // 商品id
			count: 0,
			productDetail: {},
			descList: [], // 可以选择的种类
			goodsDesc: [], // 商品描述，分段保存
			selectArr: [], // 选择的选项
			bool: false,
			sid: ''
		}
	},
	created() {
		this.getGoodsDetailData();
		this.queryCollectionGoods();
	},
	// 组件激活时重新获取
	activated() {
		this.getGoodsDetailData();
	},
	methods: {
		// 购买
		buy() {
			// 判断是否登录过
			if(!this.getToken) {

				this.$router.push({
					name: 'Login'
				})

				return ;
			}

			

			this.addShopcartByServe()
			.then(res => {
				this.$toast.clear();
				console.log(res);
				
				if(res.data.code === 3000) {
					// 立即购买时过去sid
					this.sid = res.data.sid;
					this.$toast(res.data.msg);	
					setTimeout(() => {
						this.$router.push({
							name: 'Pay',
							query: {
								sids: [this.sid]
							}
						})
					}, 300);
					
					
					// 触发vuex 方法刷新购物车数量
					// this.$store.dispatch('getShopcartCount',params)
				}else {
					this.$toast(res.data.msg);
				}
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})
			

		},
		// 加入购物车
		addShopcart() {
			// 判断是否登录过
			if(!this.getToken) {

				this.$router.push({
					name: 'Login'
				})

				return ;
			}

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.addShopcartByServe()
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 3000) {
					this.$toast(res.data.msg)		
					this.$router.push({
						name: 'Menu'
					});
					// 触发vuex 方法刷新购物车数量
					this.$store.dispatch('getShopcartCount',params)
				}
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})
		},
		// 发起加入购物车请求
		addShopcartByServe() {

			// 判断是否登录过
			if(!this.getToken) {

				this.$router.push({
					name: 'Login'
				})

				return ;
			}


			let ruleArr = [...this.selectArr];

			// 商品rule格式为: 无糖/热
			let rule = ruleArr.join('/');

			console.log(rule);

			let data = utils.queryString({
				appkey: this.appkey,
				pid: this.$route.params.id,
				tokenString: this.getToken,
				count: this.count,
				rule
			})

			// let params = {
			// 	appkey: this.appkey,
			// 	tokenString: this.getToken
			// }

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			return this.axios({
				method: 'POST',
				url: '/addShopcart',
				data
			})

		},
		// 查询收藏的商品
		queryCollectionGoods() {
			// 判断是否登录过
			if(!this.getToken) {
				return ;
			}

			let params = {
				appkey: this.appkey	,
				pid: this.$route.params.id,
				tokenString: this.getToken
			}

			this.axios({
				method: 'GET',
				url: '/findlike',
				params
			})
			.then(res => {
				console.log(res);
				if(res.data.code === 1000) {
					if(res.data.result.length > 0) {
						this.bool = true
					}
				}
			})
			.catch(err => {
				console.log(err);
				
			})
		},
		// 收藏商品
		collectionGoods() {

			// 判断是否登录过
			if(!this.getToken) {

				this.$router.push({
					name: 'Login'
				})

				return ;
			}


			let str = utils.queryString({
				appkey: this.appkey	,
				pid: this.$route.params.id,
				tokenString: this.getToken
			});

			let url = this.bool ? '/notlike' : '/like';

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'POST',
				url,
				data: str
			})
			.then(res => {
				console.log(res);
				this.$toast().clear();

				if(res.data.code === 800) {
					this.$toast(res.data.msg);
					this.bool = true;
				} else if(res.data.code === 700) {
					this.$router.push({
						name: 'Login'
					})
				} else if(res.data.code === 900) {
					this.$toast(res.data.msg);
					this.bool = false;
				} 

			})
			.catch(err => {
				this.$toast().clear();
				console.log(err);	
			})

		},
		// 选择选项
		selectItem(item, typeIndex, index) {
			// 添加选择的选项
			this.$set(this.selectArr, typeIndex, item)
			// 改变选项索引，让选项高亮
			this.$set(this.descList[typeIndex], 'currentIndex', index)
		},
		// 分段保存商品描述
		getGoodsDesc() {
			this.goodsDesc = [...this.productDetail['desc'].split(/\n/)];
		},
		// 获取可选择的数据的详情
		getDescList() {
			let typeList = ['cream','milk','sugar','tem'];

			// 根据类型获取
			typeList.forEach(item => {
				if(this.productDetail[item]) {
					let obj = {
						key: this.productDetail[`${item}_desc`],
						value: this.productDetail[item].split('/'),
						currentIndex: 0 // 当前选择的选项0为选中第一项
					}

					// 默认选项，存进数组中显示出来
					this.selectArr.push(this.productDetail[item].split('/')[0]);

					this.descList.push(obj);
				}
			})

			


		},
		getGoodsDetailData() {
			// 获取商品id
			this.id = this.$route.params.id;

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'GET',
				url: '/productDetail',
				params: {
					appkey: this.appkey,
					pid: this.id
				}
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 600) {
					this.productDetail = res.data.result[0];
					this.getDescList();
					this.getGoodsDesc();
				}else {
					this.$toast(res.data.msg)
				}
			})
		}
	},
	computed: {
		...mapGetters(['getToken'])
	}

}
</script>
